import Button from "../../../../components/button/Button";
import s from "./RowTable.module.scss";

function RowTable(props) {
    return (
        <tbody className={s.tbody}>
            <tr>
                <th></th>
                <td>{props.address}</td>
            </tr>
            <tr>
                <th colspan="2"> </th>
            </tr>
            <tr>
                <td>-:</td>
                <td>{props.weekday}</td>
            </tr>
            <tr>
                <td>:</td>
                <td>{props.weekends}</td>
            </tr>
            <tr>
                <th></th>
                <td>
                    {props.number ? ' ' : '  '}
                </td>
            </tr>
            <tr>
                <th></th>
                <td>{props.number}</td>
            </tr>
            <tr>
                <td colspan="2"><Button title="" /></td>
            </tr>
        </tbody>
    );
}
export default RowTable;