import React from "react";
import s from "./Footer.module.scss";
import Mailing from "./mailing/Mailing";
import FooterList from "./footer-list/FooterList";
import Social from "./social/Social";


function Footer(props) {

   return (
    <div className={s.footer}>
        <div className="container">
            <div className={s.footerContent}>
                <div className={s.top}>
                    <Mailing></Mailing>
                    <FooterList className={s.info} title='' value1=' ' value2='' value3='' value4=''/>
                    <FooterList className={s.magazin} title='-' value1='  ' value2='' value3='-' value4=''/>
                    <Social></Social>
                </div>
                <div className={s.bottom}>
                    <a className={s.footerLink} href='#'> </a>
                    <a className={s.footerLink} href='#'>   </a>
                </div>
            </div>    
        </div>
    </div>
    
);
}

export default Footer;
